<template>
    
    <div>

        <v-card>
            <v-row class="pa-4 px-6">
                <v-col cols="6" class="mb-0 pb-0">
                    <user-model-select @getUser_Father="getUser" ></user-model-select>
                </v-col>
                <v-col cols="6" class="mb-0 pb-0 text-right">
                    <edit-ipv @single_Father="single_Change"></edit-ipv>
                    <v-btn @click="doingNum" class="ml-3 btn-purple">計算</v-btn>
                </v-col>
            </v-row>
        </v-card>

        <v-card class="">
            <v-row class="pa-4 px-6">

                <v-col class="pa-0">
                    
                    <slot name="tree_header"></slot>

                    <div class="mt-4"></div>
                </v-col>
                <v-col cols="12" class="pa-0" v-if="show">

                        <!-- 子类地方  -->

                        <slot name="tree"></slot>

                        <!-- 子类结束 -->

                </v-col>
                <v-col cols="12" class="pt-0" v-else>

                    计算中...

                </v-col>

            </v-row>
        </v-card>
    </div>

</template>

<script>

import UserModelSelect from '@/components/Form/UserModelSelect.vue'
import EditIpv from '../Modal/EditIpv.vue'

    export default {
        components: { UserModelSelect, EditIpv },
        name: '',
        data() {
            return {
                show: false,
                user_code: 's'
            }
        },
        mounted() {

        },
        methods: {
            doingNum() {
                this.$emit('numing_Father')
            },
            getUser(code) {
                
                let res = 'ENROLLER'
                if (code == 's') { res = 'SPONSER' } 
                
                this.user_code = code
                this.$emit('user_Father', res)
            },
            single_Change() {
                this.getUser(this.user_code)
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


