<template>
    
    <div class="username" :class="{ 'tree-root': the_item.index < 2 }">

        <div class="level-space" v-for="i in the_item.index" :key="i"></div>
        <div class="inner-name d-inline">{{ the_item.name }}</div>
    </div>

</template>

<script>
    export default {
        props: {
            the_item: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
    .inner-name
        padding: 0px 12px
    .level-space
        width: calc( 24px + 2vw )
        height: 6px
        display: inline-block
</style>


