<template>
    <div>
        <v-select label="用戶模型" dense class="user-model" v-model="v" :items="choise" @change="getUserModel" 
        
          outlined
        ></v-select>
    </div>
</template>

<script>
    export default {
        props: {
            def: {
                type: String,
                default: 's'
            }
        },
        name: '',
        data() {
            return {
                v: '',
                choise: [ { text: 'Sponser', value: 's' }, { text: 'Enroller', value: 'e' } ]
            }
        },
        mounted() {
            this.v = this.def
        },
        methods: {
            getUserModel() {
                this.$emit('getUser_Father', this.v)
            }
        }
    }
</script>

<style lang="sass" scoped>
    .user-model
        width: 120px
</style>


