<template>
    
    <v-dialog
    transition="dialog-bottom-transition"
    max-width="400"
    >
        <template v-slot:activator="{ on, attrs }">
            <v-btn
                v-bind="attrs"
                v-on="on"
                outlined 
                class="btn-accent"
            >
                編輯 IPV
            </v-btn>
        </template>
        <template v-slot:default="dialog">
            <v-card>
            <v-toolbar
                color="primary"
                dark
            >Edit Someone's Ipv</v-toolbar>
            <v-card-text>
                <div class="pa-6">

                    <v-form
                        ref="form"
                    >
                        <v-container fluid>
                            <v-row>
                                <v-col
                                cols="12"
                                sm="6"
                                >
                                    <v-text-field
                                        v-model="unid"
                                        :rules="rule"
                                        label="Unid"
                                        color="secondary"
                                        required clearable
                                    ></v-text-field>
                                </v-col>
                                <v-col
                                cols="12"
                                sm="6"
                                >
                                    <v-text-field
                                        v-model="ipv"
                                        :rules="rule"
                                        color="secondary"
                                        label="Ipv"
                                        required clearable
                                    ></v-text-field>
                                </v-col>
                            </v-row>
                        </v-container>
                    </v-form>

                </div>
                <v-alert
                dense
                text

                class="ma-0"
                :type="alert_type"

                v-if="alert"
                >
                    {{ alert_msg }}
                </v-alert>
            </v-card-text>
            <v-card-actions class="justify-end">
                <v-btn
                    text
                    color="secondary dark"
                    @click="doSubmit"
                >Submit</v-btn>
                <!-- v-btn
                    text
                    @click="dialog.value = false"

                    class="text-primary-dark"
                >Close</v-btn -->
            </v-card-actions>
            </v-card>
        </template>
    </v-dialog>

</template>

<script>
import user_data from '@/apis/mock/user_unit_data'

    export default {
        name: '',
        data() {
            /*
            const defaultForm = Object.freeze({
                unid: null,
                ipv: null,
            })
            */

            return {
                // form: Object.assign({}, defaultForm),
                unid: null,
                ipv: null,
                
                rules: {
                    unid: [
                        val => val < 0 || 'Value > 0, pls.'
                    ],
                    ipv: [
                        val => val < 0 || 'Value > 0, pls.'
                    ]
                },

                rule: [ v => v > 0 || 'Value > 0, pls.' ],

                snackbar: false,

                alert: false,
                alert_type: 'success',
                alert_msg: ''
                // defaultForm
            }
        },
        computed: {
            formIsValid () {
                return (
                    this.unid &&
                    this.ipv
                )
            },
        },
        methods: {
            alertIt(msg, at = 'success') {

                if (at != 'success') {
                    this.alert_type = 'error'
                }
                this.alert = true
                this.alert_msg = msg


                setTimeout(() => {
                    this.alert = false
                }, 1500)
            },
            doSubmit() {
                let u = this.unid.trim()
                let p = this.ipv.trim()

                if ((u == null) || (p == null))  {
                    this.alertIt('Input something, pls.', '')
                    return 
                }
                if (isNaN(u) || isNaN(p)) {
                    this.alertIt('Sir, it not an number.', '')
                    return 
                }

                this.submit(u, p)
            },
            submit(u, pv) {
                const buy = [
                    { num: 1, pv }
                ]
                
                user_data.map(e => {
                    if (u == e.unid) {
                        e.buys = buy
                        this.alertIt('Change Success', 'success')
                    }
                    return e
                })

                this.$emit('single_Father')
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


