import vip_and_ratio from './level/vip_and_ratio'
import util from './util'

import conf from './conf'
const NAMESPACE = conf.NAMESPACE
const CAN_USER_PV_LIMIT = conf.CAN_USER_PV_LIMIT

const _rolling = function(ul) {
    let res = { num: 0, pv: 0 }
    let ipv = 0
    let inum = 0

    ul.map(e => {
        let res_son = { num: 0, pv: 0 }
        ipv = util.numd_pv(e)
        inum = util.numd_inum(ipv, CAN_USER_PV_LIMIT)

        if (util.judge_has_children(e)) {
            res_son = _rolling(e[NAMESPACE.children])
        }

        e[NAMESPACE.ipv] = ipv 
        e[NAMESPACE.son_pv] = res_son.pv,
        e[NAMESPACE.son_can_num] = res_son.num
        e[NAMESPACE.vip] = vip_and_ratio.get_vip(ipv, res_son.num, res_son.pv)

        res.pv += (res_son.pv + ipv)
        res.num += (res_son.num + inum)

        e.show = true
    })

    return res
}

const rolling = function(ul) {
    if (util.judge_has_children(ul)) {
        _rolling([ ul ])
    } else {
        _rolling(ul)
    }

    // console.log(ul)
}

export default rolling


