


import conf from './conf'
const NAMESPACE = conf.NAMESPACE
const CAN_USER_PV_LIMIT = conf.CAN_USER_PV_LIMIT
// 加法
const floatAdd = function(arg1, arg2) {
    let r1, r2, m
    try { r1 = arg1.toString().split(".")[1].length } catch (e) { r1 = 0 }
    try { r2 = arg2.toString().split(".")[1].length } catch(e){ r2 = 0 }
    m = Math.pow(10, Math.max(r1, r2))
    return (arg1 * m + arg2 * m) / m
}

// 浮点乘法
const floatMul = function(arg1, arg2 ) {
    let m = 0, s1 = arg1.toString(), s2 = arg2.toString()
    try { m += s1.split(".")[1].length } catch (e) { }
    try { m += s2.split(".")[1].length } catch (e) { }
    return Number(s1.replace(".", "")) * Number(s2.replace(".", "")) / Math.pow(10, m)
}

// 判断是否 有 子类
const judge_has_children = function(user) {
    if (NAMESPACE.children in user) { 
        if (user[
                NAMESPACE.children
            ].length > 0
        ) { return true }
    } return false }

// 计算 pv 奖励
const num_reward = function(pv, rag, way = true) {
    if (pv == undefined || rag == undefined) {
        return 0
    }
    if (way) {

        return floatMul(pv, rag)
    }
    return floatAdd(pv, rag)
}

// 计算购买总 Pv
const numd_pv = function(item) {
    let i = 0 

    if (
        item[
            NAMESPACE.buys
        ] && 
        item[
            NAMESPACE.buys
        ].length >= 0
    ) {

        item[
            NAMESPACE.buys
        ].map(e => { i += (e.num * e.pv); return e })
    }
    return i
}

// 判断 PV 是否足够
const judge_number = function(n, limit) { 
    if (n >= limit) { return true } return false }

// 判断是否合资格
const numd_inum = function(ipv, limit) {
    if (judge_number(ipv, limit)) { return 1 } return 0 }


export default {
    judge_has_children,
    numd_pv,
    num_reward,
    numd_inum,
    judge_number,
    floatAdd,
    floatMul
}


