const vip_0 = {
    name: '普通成员',
    type: 'simpler',
    star: 0,
    ratio: [ 0 ]
}

const vips_simpler = [
    { name: '一级会员', type: 'simpler', star: 1 },
    { name: '二级会员', type: 'simpler', star: 2 },
    { name: '三级会员', type: 'simpler', star: 3 },
    { name: '四级会员', type: 'simpler', star: 4 },
    { name: '五级会员', type: 'simpler', star: 5 },
]
const vips_leader = [
    { name: '一星领袖', type: 'leader', star: 1 },
    { name: '二星领袖', type: 'leader', star: 2 },
    { name: '三星领袖', type: 'leader', star: 3 },
    { name: '四星领袖', type: 'leader', star: 4 },
    { name: '五星领袖', type: 'leader', star: 5 },
]

const zero = 0

const ratio_design = [
    0.07, 0.06, 0.05, 0.03, 0.02
]

const ratios = {
    'simpler': [
        [ ratio_design[0], zero, zero, zero, zero, zero, zero],
        [ ratio_design[0], ratio_design[1], zero, zero, zero, zero, zero],
        [ ratio_design[0], ratio_design[1], ratio_design[1], zero, zero, zero, zero],
        [ ratio_design[0], ratio_design[1], ratio_design[1], ratio_design[2], zero, zero, zero],
        [ ratio_design[0], ratio_design[1], ratio_design[1], ratio_design[2], ratio_design[2], zero, zero],
    ],
    'leader': [
        [ ratio_design[0], ratio_design[1], ratio_design[1], ratio_design[2], ratio_design[2], ratio_design[3], zero],
        [ ratio_design[0], ratio_design[1], ratio_design[1], ratio_design[2], ratio_design[2], ratio_design[3], ratio_design[4]],
        [ ratio_design[0], ratio_design[1], ratio_design[1], ratio_design[2], ratio_design[2], ratio_design[3], ratio_design[4]],
        [ ratio_design[0], ratio_design[1], ratio_design[1], ratio_design[2], ratio_design[2], ratio_design[3], ratio_design[4]],
        [ ratio_design[0], ratio_design[1], ratio_design[1], ratio_design[2], ratio_design[2], ratio_design[3], ratio_design[4]],
    ]
}

const get_team_usd_ratio = function(type, star) {
    const i = star - 1
    return ratios[type][i]

}

const get_vip_and_ratio = function(type, star, need_ratio = true) {

    let res = vip_0

    if (type == 'leader') {
        vips_leader.map(e => {
            if (e.star == star) {
                res = e
            }
        })
    } else {
        vips_simpler.map(e => {
            if (e.star == star) {
                res = e
            }
        })
    }

    if (need_ratio && star > 0) {
        res.ratio = get_team_usd_ratio(type, star)
    }
    return res
}

const _judge_leader = function(ipv, num, pv_son) {
    const pv = ipv + pv_son

    let star = 3
    if ((num > 4) && (pv > 25000) && (pv_son > 5000)) {

        if ((num >= 6) && (pv >= 150000) && (pv_son >= 25000)) {
            star = 5
        } else if ((num >= 5) && (pv >= 72000) && (pv_son >= 9000)) {
            star = 4
        }

    } else if ((num < 4) || (pv < 25000) || (pv_son < 5000)) {

        if ((num >= 3) && (pv >= 9000) && (pv_son >= 3000)) {
            star = 2
        } else {
            star = 1
        }
    }

    return star
} 

const _judge_simpler = function(ipv, num, pv_son) {
    const pv = ipv + pv_son

    let star = 3
    if (pv > 1000) {

        if (pv >= 3000) {
            star = 5
        } else if (pv >= 2000 ) {
            star = 4
        }

    } else if (pv < 1000) {

        if (pv < 500) {
            star = 1
        } else {
            star = 2
        }

    }
    return star
} 

const get_vip = function(ipv, num, pv_son) {

    let type = 'simpler'
    let star = 1

    if (ipv < 100) {
        star = 0
    } else {


        if ((pv_son > 2000) && (num >= 2) && (ipv + pv_son) >= 5000) {

            star = _judge_leader(ipv, num, pv_son)
            type = 'leader'
            
        } else {

            star = _judge_simpler(ipv, num, pv_son)
        }
    }

    return get_vip_and_ratio(type, star)
}


export default {
    get_vip,
}


